<template>
    <article>
        <header>
            <NavBar leftColor="#fff" bgc="#e8c269" blob="600" color="#fff">
                <template #title>订单详情</template>
            </NavBar>
        </header>
        <main>
            <div class="showTitle">
                <div class="left">
                    <img src="@/assets/wait_deliver.png" alt="">
                </div>
                <div class="right">
                    {{order.state_text}}
                </div>
            </div>
            <div class="box">
                <div class="address">
                    <div class="top">
                        <span>{{address.name}}</span><span>{{address.phone}}</span>
                    </div>
                    <div class="bottom">
                        {{city}}
                    </div>
                </div>
                <div class="goodsList">
                    <div>
                        <div class="orderItem" v-for="(item, index) in goodsList" :key="item.goods_id"
                        :class="{marginBottom: index !== (goodsList.length - 1)}"
                        @click="$router.push(`/prodetail/${item.goods_id}`)"
                        >
                        <div class="left">
                            <img :src="item.goods_image" alt="">
                        </div>
                        <div class="middle">
                            <h3 class="ellipsis">{{item.goods_name}}</h3>
                        </div>
                        <div class="right">
                            <span>￥{{item.goods_price}}</span>
                            <span>×{{item.total_num}}</span>
                        </div>
                        </div>
                    </div>
                </div>
                <div class="orderInfomation">
                    <div>
                        <span class="space">订单编号</span>{{order.order_no}}<strong @click="copyOrderNumber">复制</strong>
                    </div>
                    <div>
                        <span class="space">下单时间</span>{{order.pay_time}}
                    </div>
                    <div>
                        <span class="space">买家留言</span>
                        <span v-if="order.order_price">{{order.buyer_remark}}</span>
                        <span v-else>- -</span>
                    </div>
                </div>
                <div class="orderMoney">
                    <div class="price">
                        <span>订单金额</span><span>￥{{order.order_price}}</span>
                    </div>
                    <div class="postprice">
                        <span>运费</span><span>+￥{{order.express_price}}</span>
                    </div>
                    <div class="margin"></div>
                    <div class="totalprice">
                        实付款 <span>￥{{order.pay_price}}</span>
                    </div>
                </div>
            </div>
        </main>
        <footer>
            <button v-if="order.state_text !== '待取消'" @click="cancelOrderAction">申请取消</button>
            <span v-else>取消申请中</span>
        </footer>
    </article>
</template>

<script>
import { getOrderDetail } from '@/api/orderdetail'
import { cancelOrder } from '@/api/order'
import { Toast } from 'vant'
export default {
  name: 'orderDetail',
  data () {
    return {
      order: {},
      address: {},
      city: '',
      goodsList: []
    }
  },
  methods: {
    async getOrderDetailAction () {
      const { data: { order } } = await getOrderDetail(this.orderId)
      this.order = order
      this.address = order.address
      this.goodsList = order.goods
      this.city = Object.values(order.address.region).join(' ') + ' ' + order.address.detail
    },
    async cancelOrderAction () {
      this.$dialog.confirm({
        title: '友情提示',
        message: '您确定取消订单吗'
      })
        .then(async () => {
          const res = await cancelOrder(this.order.order_id)
          Toast(res.message)
          this.$router.go(0)
        })
        .catch(() => {

        })
    },
    async copyOrderNumber () {
      try {
        const text = this.order.order_no
        await navigator.clipboard.writeText(text)
        Toast('复制成功')
      } catch (err) {
        Toast('复制失败')
        console.log(err)
      }
    }
  },
  computed: {
    orderId () {
      return this.$route.params.id
    }
  },
  created () {
    this.getOrderDetailAction()
  }
}
</script>

<style lang="less" scoped>
main {
    overflow: auto;
    position: fixed;
    top: 49.5px;
    left: 0;
    right: 0;
    bottom: 60px;
    background-color: #f4f4f4;
    .showTitle {
        height: 120px;
        background: #e8c269;
        padding: 0 15px;
        display: flex;
        align-items: center;
        .left {
             margin-top: -20px;
            img {
                width: 60px;
            }
            margin-right: 15px;
        }
        .right {
            margin-top: -20px;
            font-size: 18px;
            color: #fff;
            font-weight: 700;
        }
    }
    .box {
        padding: 0 12px;
        .address {
            margin-top: -25px;
            border-radius: 10px;
            padding: 15px;
            background: #fff;
            .top {
                font-size: 14px;
                margin-bottom: 8px;
                span {
                    &:nth-child(2) {
                        font-weight: 200;
                        margin-left: 6px;
                    }
                }
            }
            .bottom {
                font-size: 12px;
                color: gray;
            }
        }
        .goodsList {
            margin-top:12px ;
            border-radius: 10px;
            background-color: #fff;
            padding: 15px;
        }
        .orderInfomation{
            background: #fff;
            padding: 15px;
            font-size: 12px;
            border-radius: 10px;
            margin-top: 12px;
            > div {
                .space {
                    color: gray;
                    margin-right: 20px;
                }
                &:nth-child(1) {
                    strong {
                        vertical-align: top;
                        display: inline-block;
                        font-size: 10px;
                        font-weight: 400;
                        padding:2px 6px;
                        border: 1px solid gray;
                        border-radius: 9px;
                        margin-left: 10px;
                        &:active {
                            background-color: gray;
                        }
                    }
                }
                &:nth-child(2) {
                    margin: 12px 0;
                }
            }
        }
        .orderMoney {
            background: #fff;
            padding: 15px;
            font-size: 12px;
            border-radius: 10px;
            margin-top: 12px;

            .price,.postprice {
                display: flex;
                justify-content: space-between;
                margin-bottom: 12px;
                span {
                    &:nth-child(1) {
                        color: gray;
                    }
                }
            }
            .margin {
                height: 0.1px;
                background: #ececec;
                margin-bottom: 12px;
            }
            .totalprice {
                font-size: 14px;
                text-align: right;
                span {
                    color: red;
                }
            }
        }
    }
}
footer {
    background: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 60px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 15px;
    font-size: 14px;
    button {
        width: 90px;
        height: 34px;
        background: transparent;
        border: 1px solid gray;
        border-radius: 17px;
    }
}
// 无语
.marginBottom {
  margin-bottom: 25px;
}
.orderItem {
    display: flex;
    .left {
        border-radius: 5px;
        overflow: hidden;
        height: 90px;
        width: 90px;
        img {
            width: 90px;
        }
        margin-right: 10px;
    }
    .middle {
      flex: 1;
        margin-top: 5px;
       h3 {
        font-size: 12px;
        font-weight: 400;
       }
    }
    .right {
        margin-top: 5px;
        width: 70px;
        display: flex;
        flex-direction: column;
        text-align: right;
        color: gray;
        font-size: 12px;
        span {
            &:nth-child(1) {
                margin-bottom: 10px;
            }
        }

    }
}
</style>
